body {
  background-color: #3498DB;
}

* {
  box-sizing: border-box;
}

.login {
  width: 300px;
  margin: 20px auto;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.login-screen {
  background-color: white;
  margin-top: 150px;
  padding: 20px;
  border-radius: 5px;
}

.app-title {
  font-family: iransans, arial, helvetica, sans-serif;
  color: #777777;
  text-align: center;
}

.login-form {
  text-align: center;
}

.group-control {
  margin-bottom: 10px;
}

.login-field {
  width: 250px;
  height: 50px;
  background-color: #ecf0f1;
  border-radius: 5px;
  border: 2px solid transparent;
  padding: 10px;
  font-size: 20px;
  font-family: iransans, arial, helvetica, sans-serif;
  text-align: center;
  transition: all ease-in-out 0.3s;
}

.login-field:focus, .login-field:focus-visible {
  border: 2px solid #3498DB !important;
  z-index: 2;
}

.login-btn {
  width: 250px;
  height: 50px;
  background-color: #3498DB;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
  font-family: iransans, arial, helvetica, sans-serif;
  text-align: center;
  color: white;
  border: none;
  cursor: pointer;
  transition: all ease-in-out 0.3s;
}

.login-btn:hover {
  background-color: #267db8;
}